@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500&display=swap');


*{
  margin : 0;
  padding : 0;
  box-sizing : border-box;
  font-family: 'Ubuntu', sans-serif;
}
.main_div{
  width: 100%;
  height: 100vh;
  /* background: url(./video/vid.mp4); */
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}

video{
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  position: absolute;
  z-index: -1;
}

.center_div{
  width: 25%;
  height: 80vh;
  /* background: transparent; */
  /* background-color: #000; */
  /* opacity: .4; */
  background: rgba(0, 0, 0, 0.5);
  box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
  border-radius: 15px;
}

h1{
  color: white;
  background: transparent;
  background-color: #ffbd06;
  padding: 6px 0 2px 0;
  margin-bottom: 10px;
  box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.3);
}

input{
  text-align: center;
  height: 30;
  top: 10px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  border-bottom: 2px solid #f4f4f4;
  outline: none;
  color: #fff;
}

button{
  min-height: 40px;
  width: 40px;
  border-radius: 50%;
  border-color: transparent;
  background-color: #ffbd06;
  color: #fff;
  font-size: 40px;
  border: none;
  outline: none;
  margin-left: 10px;
  box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.3);
}

button:hover{
  background-color: #20bf6b;
}

ol{
  margin-top: 30px;
}

ol li{
  padding-left: 0px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: #fff;
  text-transform: capitalize;
}

.todo_style{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}

.todo_style .fa-times{
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffbd06;
  border-radius: 50%;
  margin: 0 15px 0 35px;
  color: aliceblue;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
}

.todo_style .fa-times:hover{
  background-color: crimson;
}

.todo_style .fa-times:hover .list_style{
  color: crimson;
}

@media screen and (max-width: 1024px) {
  .center_div{
    width: 35%;
  }
}
@media screen and (max-width: 768px) {
  .center_div{
    width: 40%;
  }
}
@media screen and (max-width: 768px) {
  .center_div{
    width: 80%;
  }
}
